html {
  background-image: -webkit-gradient(linear, left top, left bottom, from(rgba(0, 0, 0, 0.13)), to(rgba(0, 0, 0, 0.603))), url(/static/media/faded-laptop-in-background-with-html-code-highlighted-on-the-screen.5b7c6ca2.jpg);
  background-image: linear-gradient(rgba(0, 0, 0, 0.13), rgba(0, 0, 0, 0.603)), url(/static/media/faded-laptop-in-background-with-html-code-highlighted-on-the-screen.5b7c6ca2.jpg);
  height: 100vh;
  background-repeat: repeat;
  background-size: cover;
  background-position: center;
}
body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.main-nav {
    margin-left: 470px;
    list-style: none;
    margin-top: 15px;
}

.main-nav li {
    display: inline-block;
}
.main-nav li a {
    color: white;
    text-decoration: none;
    padding: 5px 50px;
    font-size: 15px
}
.main-nav li a:hover {
    border: 1px solid white;
}
.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

